ul{
    display: inline-block;
}
.com ul li{
    display: inline-block;
    border:1px solid #DEF1E5;
    background-color: white;
    height:250px;
    width:150px;
    margin-top: 20px;
    margin-left: -10px;
    margin-right: 25px;
    border-radius: 10px;
}
.com ul li img{
    width:150px;
    height:180px;
    border-radius: 10px 10px 0 0;
}
.com ul li p{
    font-size: smaller;
}
.com ul li div span{
    font-size: x-small;
    color:rgb(236, 199, 199);
}
.com ul li div p{
    color:#ccc;
    margin-top: -13px;
    padding-left: 100px;
}
